.container {
    padding: 50px 0;
}

.header {
    margin: 0;
    text-align: center;
    padding:10px 0;
    font-size: 48px;
    font-weight: bold;
}
.movies_wrapper {
    max-width: 1280px;
    padding: 50px 40px 100px;
    margin: 0 auto;
}

.rating_wrapper {
  text-align: end;
  font-size: 2rem;
  max-width: 1280px;
  margin: 0 auto;
  padding: 1rem 0;

}

.rating_wrapper select {
  font-size: 1.5rem;
  padding: 0.5rem;
  margin-left: 1rem;
}

@media all and (max-width:820px)  {

  .movies_wrapper {
    max-width: none;
  }

  .rating_wrapper {
    max-width: none;
    margin-right: 2rem;
    font-size: 1.8rem;
  }

  .rating_wrapper select {
    font-size: 1.3rem
  }
    
}